<template>
    <div class="tuoguan">
        <div class="gh-table" v-if="showTable">
            <div v-if="!showSetting">
                <el-button class="create" type="primary" @click="create">接管新公号</el-button>
                <el-button style="float:right;" type="warning" @click="reset">
                    <span style="color: #000;">(慎用)</span>
                    重置code
                </el-button>
                <el-table :data="gonghaoList" border style="width: 100%">
                    <el-table-column header-align="center" align="center" prop="code" label="code" width="55">
                    </el-table-column>
                    <el-table-column header-align="center" align="center" prop="name" label="公号名称" width="80">
                    </el-table-column>
                    <el-table-column header-align="center" align="center" prop="appid" label="appid" width="120">
                    </el-table-column>
                    <el-table-column header-align="center" align="center" prop="appsecret" label="appsecret" width="170">
                    </el-table-column>
                    <el-table-column header-align="center" align="center" prop="token" label="token" width="85">
                    </el-table-column>
                    <el-table-column header-align="center" align="center" prop="EncodingAESKey" label="EncodingAESKey" width="210">
                    </el-table-column>
                    <el-table-column header-align="center" align="center" prop="url" label="链接" width="150">
                        <template slot-scope="scope">
                            <span>{{'http://www.rrdtjj.top/weichat/' + scope.row.code}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <el-button @click="update(scope.row)" type="primary" size="small">编辑</el-button>
                            <el-button @click="setting(scope.row)" type="warning" size="small">配置</el-button>
                            <el-button @click="remove(scope.row, scope.$index)" type="danger" size="small">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class="setting-container" v-else>
                <setting @showAgain="showAgain" :code="code">{{name}}</setting>
            </div>
        </div>
        <div class="edit-container" v-else>
            <edit-info @showAgain="showAgain" :data_info="isCreate ? {token: 'mingxingshuo'} : data_info">{{isCreate ? '接管新公号' : '接管公号信息修改'}}</edit-info>
        </div>
    </div>
</template>

<script>
    import editInfo from "./tuoguan/editInfo.vue";
    import setting from "./tuoguan/setting.vue";
    import Axios from "axios";
    import store from "../../store.js";
    export default {
      computed: {
        showTable() {
          return store.state.showTable;
        },
        showSetting() {
          return store.state.showSetting;
        }
      },
      components: {
        editInfo,
        setting
      },
      data() {
        return {
          isCreate: true,
          gonghaoList: [],
          data_info: {},
          code: null,
          name: ""
        };
      },
      mounted() {
        this.showDataList();
      },
      methods: {
        create() {
          this.openPage();
        },
        update(row) {
          this.openPage();
          this.isCreate = false;
          store.commit("changeGonghaoUpdate", true);
          this.data_info = row;
        },
        remove(row, index) {
          this.$confirm("确认删除？")
            .then(_ => {
              Axios({
                url: "/conf/del",
                method: "get",
                params: {
                  id: row._id
                }
              }).then(res => {
                this.gonghaoList.splice(index, 1);
                this.$message({
                  type: "success",
                  message: res.data.success
                });
              });
            })
            .catch(_ => {});
        },
        openPage() {
          store.commit("changeShowTable", false);
        },
        closePage() {
          store.commit("changeShowTable", true);
        },
        showDataList() {
          Axios({
            url: "/conf",
            method: "get"
          }).then(res => {
            this.gonghaoList = res.data.data;
          });
        },
        showAgain() {
          this.showDataList();
        },
        reset() {
          this.$confirm("确认重置code吗？")
            .then(_ => {
              Axios({
                url: "/conf/reset",
                method: "get"
              }).then(res => {
                this.$message({
                  type: "success",
                  message: res.data.success
                });
              });
            })
            .catch(_ => {});
        },
        setting(row) {
          this.code = row.code;
          this.name = row.name;
          store.commit("changeShowSetting", true);
        }
      }
    };
</script>

<style scoped>
    .tuoguan {
      margin-left: 200px;
      padding: 30px;
    }

    .create {
      margin-bottom: 30px;
    }
</style>



