<template>
    <div class="slider-bar">
        <el-col :span="12">
            <el-menu :default-active="sliderIndex" class="el-menu-vertical" background-color="#68717a" text-color="#fff" active-text-color="#ffd04b" @select="handleSelect1">
                <el-menu-item v-for="(item, index) in sliderBar" :key="index" :index="index.toString()">
                    <i :class="sliderIndex == index ? ('active ' + item.classname)  : item.classname"></i>
                    <span slot="title">{{item.title}}</span>
                </el-menu-item>
            </el-menu>
        </el-col>
    </div>
</template>

<script>
    export default {
      props: {
        sliderBar: Array
      },
      data() {
        return {
          sliderIndex: "0"
        };
      },
      mounted() {},
      methods: {
        handleSelect1(key, keyPath) {
          this.sliderIndex = key;
          this.$emit("select", key);
        }
      }
    };
</script>


<style scoped>
    .slider-bar {
      position: fixed;
      top: 61px;
      left: 0;
      height: 100vh;
    }
    .slider-bar .el-menu-vertical {
      width: 200px;
      color: #68717a;
      height: 100vh;
    }

    .slider-bar .el-menu-vertical i {
      color: #fff;
    }

    .slider-bar .el-menu-vertical i.active {
      color: #ffd04b;
      margin-right: 5px;
      width: 24px;
      text-align: center;
      font-size: 18px;
      vertical-align: middle;
    }
</style>



