<template>
    <div class="novel">
        <slider-bar :sliderBar="sliderBarList" @select="select"></slider-bar>
        <div class="container">
            <novel-link v-if="showIndex == 0"></novel-link>
            <push-links v-if="showIndex == 1"></push-links>
            <qr-code v-if="showIndex == 2"></qr-code>
            <tag-management v-if="showIndex == 3"></tag-management>
        </div>
    </div>
</template>

<script>
    import sliderBar from "../components/common/sliderBar.vue";
    import novelLink from "../components/novel/novelLink.vue";
    import pushLinks from "../components/novel/pushLinks.vue";
    import qrCode from "../components/novel/qrCode.vue";
    import tagManagement from "../components/novel/tagManagement.vue";
    export default {
      name: "ghManagement",
      components: {
          sliderBar,
          novelLink,
          pushLinks,
          qrCode,
          tagManagement
      },
      data() {
        return {
          showIndex: 0,
          sliderBarList: [
            { classname: "el-icon-tickets", title: "小说链接" },
            { classname: "el-icon-news", title: "投放链接" },
            { classname: "el-icon-picture-outline", title: "参数二维码" },
            { classname: "el-icon-rank", title: "标签管理" }
          ]
        };
      },
      methods: {
        select(key) {
          this.showIndex = key;
        }
      }
    };
</script>

<style scoped>
    .novel {
      margin-top: 61px;
    }
    .container {
        margin-left: 200px;
        padding: 30px;
    }
</style>



