<template>
    <div>
        <h2 class="title">
            <slot></slot>
        </h2>
        <div class="edit-info">
            <el-form :model="formData" status-icon label-width="200px" class="form-group">
                <el-form-item label="code" prop="code">
                    <el-input type="text" v-model="code"></el-input>
                </el-form-item>
                <el-form-item label="二级菜单" prop="has_sub_btn">
                    <el-switch v-model="has_sub_btn" active-color="#13ce66">
                    </el-switch>
                </el-form-item>
                <el-form-item label="一级菜单名称" prop="name">
                    <el-input type="text" v-model="formData.name"></el-input>
                </el-form-item>
                <!-- 没有二级菜单 -->
                <el-form-item label="链接/关键词" v-show="!has_sub_btn">
                    <el-radio v-model="formData.type" label="view">view</el-radio>
                    <el-radio v-model="formData.type" label="click">click</el-radio>
                </el-form-item>
                <el-form-item label="一级菜单链接" prop="url" v-show="!has_sub_btn && formData.type == 'view'">
                    <el-input type="text" v-model="formData.url"></el-input>
                </el-form-item>
                <el-form-item label="一级菜单关键字" prop="url" v-show="!has_sub_btn && formData.type == 'click'">
                    <el-input type="text" v-model="formData.key"></el-input>
                </el-form-item>
                <!-- 没有二级菜单   end    -->
                <!-- 有二级菜单 -->
                <el-form-item label="二级菜单名称" prop="name" v-show="has_sub_btn">
                    <el-input type="text" v-model="subItem.name"></el-input>
                </el-form-item>
                <el-form-item label="链接/关键词" v-show="has_sub_btn">
                    <el-radio v-model="subItem.type" label="view">view</el-radio>
                    <el-radio v-model="subItem.type" label="click">click</el-radio>
                </el-form-item>
                <el-form-item label="二级菜单链接" prop="url" v-show="has_sub_btn && subItem.type == 'view'">
                    <el-input type="text" v-model="subItem.url"></el-input>
                </el-form-item>
                <el-form-item label="二级菜单关键词" prop="key" v-show="has_sub_btn && subItem.type == 'click'">
                    <el-input type="text" v-model="subItem.key"></el-input>
                </el-form-item>
                <el-button type="primary" v-show="has_sub_btn && !isUpdate1" @click="addSubBtn()" style="margin-left: 200px;">添加二级菜单</el-button>
                <el-button type="success" v-show="isUpdate1" @click="saveSecond()" style="margin-left: 200px;">保存</el-button>
                <span class="line" v-show="has_sub_btn"></span>
                <el-button type="primary" @click="addBtn()" style="margin-left: 200px;">添加一级菜单</el-button>
                <el-button type="success" @click="saveFirst()" v-show="isUpdate" style="margin-left: 30px;">保存</el-button>
                <el-table :data="values" border style="width: 100%;margin:30px;">
                    <el-table-column header-align="center" align="center" label="一级菜单名称" width="200">
                        <template slot-scope="scope">
                            {{scope.row.name ? scope.row.name : '~'}}
                        </template>
                    </el-table-column>
                    <el-table-column label="操作" header-align="center" align="center">
                        <template slot-scope="scope">
                            <el-button @click="updateFirst(scope.row)" type="primary" size="small">修改</el-button>
                            <el-button @click="removeFirst(scope.$index)" type="danger" size="small">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <el-form-item>
                    <el-button type="primary" @click="submitForm()">提交</el-button>
                    <el-button type="info" @click="cancel()">取消</el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="sub-btn-table">
            <el-table :data="formData.sub_button" border style="width: 100%;z-index:-1;">
                <el-table-column header-align="center" align="center" prop="name" label="二级菜单名称" width="200"></el-table-column>
                <el-table-column label="操作" header-align="center" align="center">
                    <template slot-scope="scope">
                        <el-button @click="updateSecond(scope.row)" type="primary" size="small">编辑</el-button>
                        <el-button @click="removeSecond(scope.$index)" type="danger" size="small">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
    import store from "../../../store.js";
    import Axios from "axios";
    export default {
      props: {
        data_info: Object
      },
      data() {
        return {
          code: "",
          formData: {
            sub_button: [],
            type: "view"
          },
          values: [],
            has_sub_btn: false,
          subItem: {
            type: "view"
          },
          radio: "url",
          isUpdate: false,
          isUpdate1: false
        };
      },
      mounted() {
        this.isUpdate = store.state.menuUpdate;
        console.log(this.data_info)
        if (store.state.menuUpdate) {
          this.code = this.data_info.code;
          this.values = this.data_info.values;
          this.formData = this.values[0];
        }
      },
      methods: {
        cancel() {
          store.commit("changeMenuTable", true);
          store.commit("changeMenuUpdate", false);
          this.$emit("showAgain");
        },
        resetForm() {
          this.formData = {
              sub_button: [],
            type: "view"
          };
        },
        addBtn() {
            if(this.formData.sub_button.length == 0) {
                delete this.formData.sub_button
            }
          this.values.push(this.formData);
          this.formData = {
            sub_button: [],
            type: "view"
          };
        },
        submitForm() {
          if (!store.state.menuUpdate) {
            Axios({
              url: "/menu/create",
              method: "post",
              data: {
                code: this.code,
                values: this.values
              }
            }).then(res => {
              store.commit("changeMenuTable", true);
              this.$emit("showAgain");
            });
          } else {
            Axios({
              url: "/menu/update",
              method: "post",
              data: {
                id: this.data_info._id,
                code: this.code,
                values: this.values
              }
            }).then(res => {
              this.values = [];
              this.formData = {
                sub_button: [],
            type: "view"
              };
              store.commit("changeMenuUpdate", false);
              store.commit("changeMenuTable", true);
              this.$emit("showAgain");
            });
          }
        },
        addSubBtn() {
          this.formData.sub_button.push(this.subItem);
          this.subItem = {
              type: "view"
          };
          this.has_sub_btn = true
        },
        updateFirst(row) {
          this.formData = row;
        },
        updateSecond(row) {
          this.subItem = row;
          this.isUpdate1 = true;
        },
        removeFirst(index) {
          this.values.splice(index, 1);
          this.formData = {
           sub_button: [],
            type: "view"
          };
          console.log(this.formData);
        },
        removeSecond(index) {
          this.formData.sub_button.splice(index, 1);
          this.subItem = {
              type: "view"
          };
          this.has_sub_btn = true
        },
        saveFirst() {
          this.formData = {
            sub_button: [],
            type: "view"
          };
        },
        saveSecond() {
          this.isUpdate1 = false;
          this.subItem = {
              type: "view"
          };
          this.has_sub_btn = false
        }
      }
    };
</script>

<style scoped>
    .edit-info {
      float: left;
    }
    .form-group {
      width: 500px;
    }
    .title {
      margin-bottom: 50px;
      margin-left: 200px;
    }
    .sub-btn-table {
      float: left;
      width: 500px;
      margin: 150px 0 0 50px;
    }
    .line {
      width: 480px;
      height: 30px;
      margin: 30px 40px 0;
      display: block;
      border-top: 1px dashed #000;
    }
</style>



