import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        showTable: true,
        showMenu: true,
        showMsg: true,
        showSetting: false,
        settingTable: true,
        gonghaoUpdate: false,
        menuUpdate: false,
        messageUpdate: false,
        settingUpdate: false,
        isAddSwitch: false,
        isEditSwitch: false,
        isUpdateLinksSwitch: false,
        addLinksShow: false,
        goodsShow: false,
        goodsData: {},
        pagesData: {
            pagename: '请输入单页名称',
            class: '请输入class'
        },
        goodsListData: {},
        isUpdateGoodsSwitch: false
    },
    mutations: {
        changeShowTable: (state, data) => {
            state.showTable = data;
        },
        changeMenuTable: (state, data) => {
            state.showMenu = data;
        },
        changeMsgTable: (state, data) => {
            state.showMsg = data;
        },
        changeShowSetting: (state, data) => {
            state.showSetting = data;
        },
        changeSettingTable: (state, data) => {
            state.settingTable = data;
        },
        changeGonghaoUpdate: (state, data) => {
            state.gonghaoUpdate = data;
        },
        changeMenuUpdate: (state, data) => {
            state.menuUpdate = data;
        },
        changeMsgUpdate: (state, data) => {
            state.messageUpdate = data;
        },
        changeSettingUpdate: (state, data) => {
            state.settingUpdate = data;
        },
        changeAddSwitch: function(state, data) {
            state.isAddSwitch = data;
        },
        changeEditSwitch: function(state, data) {
            state.isEditSwitch = data;
        },
        changeUpdateLinksSwitch: function(state, data) {
            state.isUpdateLinksSwitch = data;
        },
        changeUpdateGoodsSwitch: function(state, data) {
            state.isUpdateGoodsSwitch = data;
        },
        changeAddLinks: function(state, data) {
            state.addLinksShow = data;
        },
        changeGoodsShow: function(state, data) {
            state.goodsShow = data;
        },
        setGoodsData(state, data) {
            if (data) {
                state.goodsData = data
            } else {
                state.goodsData = {}
            }
        },
        setPagesData(state, data) {
            if (data) {
                state.pagesData = data
            } else {
                state.pagesData = {}
            }
        },
        setGoodsListData(state, data) {
            if (data) {
                state.goodsListData = data
            } else {
                state.goodsListData = {}
            }
        }
    },
    actions: {}
});


