<template>
    <div class="edit-info">
        <h2 class="title">
            <slot></slot>
        </h2>
        <el-form :model="formData" status-icon label-width="200px" class="form-group">
            <el-form-item label="公号名称" prop="name">
                <el-input type="text" v-model="formData.name"></el-input>
            </el-form-item>
            <el-form-item label="appid" prop="appid">
                <el-input type="text" v-model="formData.appid"></el-input>
            </el-form-item>
            <el-form-item label="appsecret" prop="appsecret">
                <el-input v-model="formData.appsecret"></el-input>
            </el-form-item>
            <el-form-item label="token" prop="token">
                <el-input type="text" v-model="formData.token"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm()">提交</el-button>
                <el-button type="warning" @click="resetForm()">重置</el-button>
                <el-button type="info" @click="cancel()">取消</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import store from "../../../store.js";
    import Axios from "axios";
    export default {
      props: {
        data_info: Object
      },
      data() {
        return {
          formData: {
            token: "mingxingshuo"
          }
        };
      },
      mounted() {
        if (store.state.gonghaoUpdate) {
          this.formData = this.data_info;
        }
      },
      methods: {
        cancel() {
          store.commit("changeShowTable", true);
          store.commit("changeGonghaoUpdate", false);
          this.$emit("showAgain");
        },
        resetForm() {
          this.formData = {
            token: "mingxingshuo"
          };
        },
        submitForm() {
          if (!store.state.gonghaoUpdate) {
            Axios({
              url: "/conf/create",
              method: "post",
              data: this.formData
            }).then(res => {
              store.commit("changeShowTable", true);
              store.commit("changeGonghaoUpdate", false);
              this.$emit("showAgain");
            });
          } else {
            Axios({
              url: "/conf/update",
              method: "post",
              data: {
                id: this.data_info._id,
                code: this.data_info.code,
                name: this.formData.name,
                appid: this.formData.appid,
                appsecret: this.formData.appsecret,
                token: this.formData.token
              }
            }).then(res => {
              console.log(res);
              store.commit("changeShowTable", true);
              store.commit("changeGonghaoUpdate", false);
              this.$emit("showAgain");
            });
          }
        }
      }
    };
</script>

<style scoped>
    .edit-info {
      float: left;
    }
    .form-group {
      width: 500px;
    }
    .title {
      margin-bottom: 50px;
      margin-left: 200px;
    }
</style>



