<template>
    <div class="gh-management">
        <slider-bar :sliderBar="sliderBarList" @select="select"></slider-bar>
        <tuoguan v-if="showIndex == 0"></tuoguan>
        <menu-setting v-if="showIndex == 1"></menu-setting>
        <send-message v-if="showIndex == 2"></send-message>
    </div>
</template>

<script>
    import store from "../store.js"
    import sliderBar from "../components/common/sliderBar.vue";
    import tuoguan from "../components/ghgl/tuoguan.vue";
    import menuSetting from "../components/ghgl/menuSetting.vue";
    import sendMessage from "../components/ghgl/message.vue";
    export default {
      name: "ghManagement",
      components: {
        sliderBar,
        tuoguan,
        sendMessage,
        menuSetting
      },
      data() {
        return {
          showIndex: 0,
          sliderBarList: [
            { classname: "el-icon-menu", title: "公号托管" },
            { classname: "el-icon-setting", title: "设置菜单栏" },
            { classname: "el-icon-message", title: "回复消息" }
          ]
        };
      },
      methods: {
        select(key) {
          this.showIndex = key;
          this.reset()
        },
        reset() {
            store.commit('changeShowTable', true)
            store.commit('changeMenuTable', true)
            store.commit('changeMsgTable', true)
            store.commit('changeShowSetting', false)
            store.commit('changeSettingTable', true)
            store.commit('changeGonghaoUpdate', false)
            store.commit('changeMenuUpdate', false)
            store.commit('changeMsgUpdate', false)
            store.commit('changeSettingUpdate', false)
        }
      }
    };
</script>

<style scoped>
    .gh-management {
      margin-top: 61px;
    }
</style>



