<template>
    <div class="setting">
        <div v-if="settingTable">
            <h2 class="title">
                <slot></slot>
            </h2>
            <el-button class="create" type="primary" @click="create">配置新规则</el-button>
            <el-button style="float:right;" type="danger" @click="close">关闭</el-button>
            <el-table :data="settingList" border style="width: 100%">
                <el-table-column header-align="center" align="center" prop="type" label="类型" width="100">
                    <template slot-scope="scope">
                        {{scope.row.type === 0 ? '文本' : (scope.row.type === 1 ? '点击' : '关注')}}
                    </template>
                </el-table-column>
                <el-table-column header-align="center" align="center" prop="text" label="用户回复文本" width="150"></el-table-column>
                <el-table-column header-align="center" align="center" prop="key" label="点击关键词" width="150"></el-table-column>
                <el-table-column header-align="center" align="center" prop="msgId" label="消息msgId" width="100"></el-table-column>
                <el-table-column header-align="center" align="center" prop="showUrl" label="图片" width="200">
                    <template slot-scope="scope">
                        <img style="width: 160px;" v-if="scope.row.showUrl" :src="scope.row.showUrl" alt="">
                    </template>
                </el-table-column>
                <el-table-column label="操作" header-align="center" align="center" width="240">
                    <template slot-scope="scope">
                        <el-button @click="update(scope.row)" type="primary" size="small">编辑</el-button>
                        <el-button @click="remove(scope.row, scope.$index)" type="danger" size="small">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <edit-setting @showAgain="showAgain" v-else :data_info="data_info" :code = "code">{{isCreate ? '配置新规则' : '规则修改'}}</edit-setting>
    </div>
</template>

<script>
    import store from "../../../store.js";
    import editSetting from "./editSetting.vue";
    import Axios from "axios";
    export default {
      computed: {
        settingTable() {
          return store.state.settingTable;
        }
      },
      components: {
        editSetting
      },
      props: {
        code: Number
      },
      data() {
        return {
          settingList: [],
          isCreate: true,
          data_info: {}
        };
      },
      mounted() {
        this.showSettingList();
      },
      methods: {
        create() {
          this.isCreate = true;
          store.commit("changeSettingTable", false);
        },
        close() {
            store.commit("changeShowSetting", false);
        },
        showSettingList() {
          Axios({
            url: "/reply",
            method: "get"
          }).then(res => {
            this.settingList = res.data.data;
          });
        },
        showAgain() {
          this.showSettingList();
        },
        update(row) {
          this.isCreate = false;
          this.data_info = row;
          store.commit("changeSettingTable", false);
          store.commit("changeSettingUpdate", true);
        },
        remove(row, index) {
          this.$confirm("确认删除？")
            .then(_ => {
              Axios({
                url: "/reply/del",
                method: "get",
                params: {
                  id: row._id
                }
              }).then(res => {
                this.settingList.splice(index, 1);
                this.$message({
                  type: "success",
                  message: res.data.success
                });
              });
            })
            .catch(_ => {
              this.$message({
                type: "info",
                message: "已取消删除"
              });
            });
        }
      }
    };
</script>

<style scoped>
    .setting {
      float: left;
    }
    .form-group {
      width: 500px;
    }
    .title,
    .create {
      margin-bottom: 30px;
    }
</style>



