<template>
    <div class="edit-message">
        <el-form ref="form" label-width="80px" size="mini">
            <el-form-item>
                <h2>
                    <slot></slot>
                </h2>
            </el-form-item>
            <el-form-item label="类型">
                <el-radio-group v-model="type" size="medium" @change="change">
                    <el-radio border label="0">文本</el-radio>
                    <el-radio border label="1">图文</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form ref="subForm" :model="subForm" label-width="80px" size="mini" v-if="type == 1">
                <el-form-item label="图片">
                    <el-upload :show-file-list="false" enctype="multipart/form-data" name="imageFile" action="http://test.oorggt.top/reply/upload" :on-success="handleAvatarSuccess">
                        <el-button size="small" type="primary">点击上传</el-button>
                        <img v-if="imgUrl" :src="imgUrl" alt="" width="200" style="margin-top:10px;">
                    </el-upload>
                </el-form-item>
                <el-form-item label="标题">
                    <el-input v-model="subForm.title" style="width:500px;"></el-input>
                </el-form-item>
                <el-form-item label="链接">
                    <el-input v-model="subForm.url" style="width:500px;"></el-input>
                </el-form-item>
                <el-form-item label="详细信息" prop="description">
                    <el-input type="textarea" v-model="subForm.description" style="width:500px;"></el-input>
                </el-form-item>
                <el-form-item size="large">
                    <el-button type="success" @click="addOne">添加</el-button>
                    <el-button type="primary" @click="saveOne">保存</el-button>
                </el-form-item>
                <el-form-item label="图文消息" prop="pic_message">
                    <el-table :data="picMessageData" style="width: 100%;" border>
                        <el-table-column prop="title" label="标题" width="100"></el-table-column>
                        <el-table-column prop="picurl" label="图片" width="200" style="margin-top:30px;">
                            <template slot-scope="scope">
                                <img :src="scope.row.picurl" width="100%">
                            </template>
                        </el-table-column>
                        <el-table-column prop="url" label="链接" width="200"></el-table-column>
                        <el-table-column prop="description" label="详细信息" width="300"></el-table-column>
                        <el-table-column label="操作">
                            <template slot-scope="scope">
                                <el-button size="small" type="primary" @click="updateOne(scope.$index, scope.row)">修改</el-button>
                                <el-button size="small" type="danger" class="send-message" @click="deleteOne(scope.$index, scope.row)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-form-item>
            </el-form>
            <el-form-item v-else-if="type == 0" label="详细信息" prop="description">
                <el-input type="textarea" v-model="description" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item size="large">
                <el-button type="primary" @click="onSave">保存</el-button>
                <el-button @click="cancel">取消</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import Axios from "axios";
    import store from "../../../store.js";
    export default {
      props: {
        data_info: Object
      },
      data() {
        return {
          type: "0",
          subForm: {},
          picMessageData: [],
          contents: [],
          description: "",
          imgUrl: "",
          id: "",
          index: 0
        };
      },
      mounted() {
        if (store.state.messageUpdate) {
          this.type = this.data_info.type.toString();
          this.description = this.data_info.description;
          this.picMessageData = this.data_info.contents;
          this.subForm = this.picMessageData[0];
          this.imgUrl = this.subForm.picurl;
          this.id = this.data_info._id;
        }
      },
      methods: {
        onSave() {
          var that = this;
          if (!store.state.messageUpdate) {
            if (that.description != "") {
              that.contents.push({ description: that.description });
            } else {
              that.contents = that.picMessageData;
            }
            Axios({
              url: "/msg/create",
              method: "post",
              data: {
                type: this.type,
                description: this.description,
                contents: this.contents
              }
            }).then(res => {
              this.close();
            });
          } else {
            if (that.description != "") {
              that.contents.push({ description: that.description });
            } else {
              that.contents = that.picMessageData;
            }
            Axios({
              url: "/msg/update",
              method: "post",
              data: {
                id: this.id,
                type: this.type,
                description: this.description,
                contents: this.contents
              }
            }).then(res => {
              this.close();
            });
          }
        },
        addOne() {
          this.subForm.picurl = this.imgUrl;
          this.picMessageData.push(this.subForm);
          this.subForm = {};
        },
        saveOne() {
          this.subForm.picurl = this.imgUrl;
          this.picMessageData.splice(this.index, 1, this.subForm);
          this.subForm = {};
          this.imgUrl = "";
        },
        change() {
          this.description = "";
          this.subForm = {};
        },
        handleAvatarSuccess(res, file) {
          this.imgUrl = "http://test.oorggt.top/uploads/" + res.filename;
        },
        updateOne(index, row) {
          this.index = index;
          this.subForm = row;
          this.imgUrl = row.picurl;
        },
        deleteOne(index, row) {
          this.$confirm("此操作删除此条客服消息, 是否继续?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          })
            .then(() => {
              if (store.state.messageUpdate && this.picMessageData.length <= 1) {
                this.$message({
                  type: "info",
                  message: " 列表中最少展示一条数据"
                });
              } else {
                this.picMessageData.splice(index, 1);
                this.$message({
                  type: "success",
                  message: " 删除成功"
                });
              }
            })
            .catch(() => {
              this.$message({
                type: "warning",
                message: "已取消删除"
              });
            });
        },
        cancel() {
          this.close();
        },
        reset() {
          this.type = "0";
          this.imgUrl = this.description = "";
          this.subForm = {};
          this.picMessageData = [];
        },
        close() {
          store.commit("changeMsgTable", true);
          store.commit("changeMsgUpdate", false);
          this.$emit("showAgain");
          this.reset();
        }
      }
    };
</script>

<style scoped>
    .edit-message {
      border: 1px solid #eee;
      width: 100%;
      float: left;
      padding: 20px;
    }
    .tag-name {
      width: 100px;
      height: 30px;
      line-height: 30px;
      border: 1px solid #eee;
      border-radius: 5px;
      padding-left: 10px;
    }
    .tag-list li {
      float: left;
      margin-right: 10px;
      cursor: pointer;
    }
</style>




