<template>
    <div class="message">
        <div v-if="showMsg">
            <el-button class="create" type="primary" @click="create">添加新消息</el-button>
            <el-table :data="tableData" style="width: 100%;margin-top:30px;" border>
                <el-table-column prop="msgId"  header-align="center"  align="center"  label="msgId"  width="65"></el-table-column>
                <el-table-column prop="type"  label="类型"  header-align="center"  align="center"  width="100">
                    <template slot-scope="scope">
                        <span>{{scope.row.type == 0 ? '文本消息' : '图文消息'}}{{scope.row.contents.length > 1 ? '(多)' : ''}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="contents[0].title"  header-align="center"  align="center"  label="标题"  width="100"></el-table-column>
                <el-table-column prop="contents[0].description"  header-align="center"  align="center"  label="详细信息"  width="220"></el-table-column>
                <el-table-column prop="contents[0].url"  header-align="center"  align="center"  label="链接"  width="150"></el-table-column>
                <el-table-column prop="contents[0].picurl"  header-align="center"  align="center"  label="图片"  width="200">
                    <template slot-scope="scope">
                        <img v-if="scope.row.contents[0].picurl" :src="scope.row.contents[0].picurl" width="100%" />
                    </template>
                </el-table-column>
                <el-table-column label="操作"  header-align="center"  align="center">
                    <template slot-scope="scope">
                        <el-button size="small" type="warning" @click="copyMessage(scope.row)">复制</el-button>
                        <el-button size="small" type="primary" @click="updateMessage(scope.$index, scope.row)">修改</el-button>
                        <el-button size="small" type="danger" @click="deleteMessage(scope.row, scope.$index)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <edit-message @showAgain="showAgain" :data_info="data_info" v-else>{{isCreate ? '新增客服消息' : '编辑客服消息'}}</edit-message>
    </div>
</template>

<script>
    import Axios from "axios";
    import store from "../../store.js";
    import editMessage from "./message/editMessage";
    export default {
      computed: {
        showMsg() {
          return store.state.showMsg;
        },
        messageUpdate() {
          return store.state.messageUpdate;
        }
      },
      components: {
        editMessage
      },
      data() {
        return {
          tableData: [],
          desShow: "",
          isCreate: true,
          data_info: {}
        };
      },
      mounted() {
        this.showMessage();
      },
      methods: {
        create() {
          store.commit("changeMsgTable", false);
          this.isCreate = true;
        },
        copyMessage(row) {
          Axios({
            url: "/msg/create",
            method: "post",
            data: {
              contents: row.contents,
              type: row.type,
              description: row.description
            }
          }).then(res => {
            this.showMessage();
            console.log(res);
          });
        },
        showMessage() {
          Axios({
            url: "/msg",
            method: "get",
            parmas: {},
            withCredentials: true
          }).then(res => {
            if (res.data.data.length != 0) {
              this.tableData = res.data.data;
            }
          });
        },
        showAgain() {
          this.showMessage();
        },
        updateMessage(index, row) {
          store.commit("changeMsgTable", false);
          store.commit("changeMsgUpdate", true);
          this.isCreate = false;
          this.data_info = row;
        },
        deleteMessage(row, index) {
          this.$confirm("此操作删除此条客服消息, 是否继续?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          })
            .then(() => {
              Axios({
                url: "/msg/del",
                method: "get",
                params: {
                  id: row._id
                }
              }).then(res => {
                this.$message({
                  type: "success",
                  message: res.data.success
                });
                this.showMessage();
                this.tableData.splice(index, 1);
              });
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: "已取消删除"
              });
            });
        }
      }
    };
</script>

<style scoped>
    .message {
      margin-left: 201px;
      padding: 30px;
    }

    td.el-table_1_column_3 {
      max-height: 100px;
    }
</style>




