<template>
    <div class="home">
        <div class="nav">
            <h2 class="logo">后台管理系统</h2>
            <el-menu :default-active="activeIndex" class="el-menu" mode="horizontal" @select="handleSelect" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
                <el-menu-item index="1">公号管理</el-menu-item>
                <el-menu-item index="2">爆款单页</el-menu-item>
                <el-menu-item index="3">小说管理</el-menu-item>
                <el-menu-item index="4">数据统计</el-menu-item>
            </el-menu>
        </div>
        <gh-management v-if="activeIndex == '1'"></gh-management>
        <baokuan v-if="activeIndex == '2'"></baokuan>
        <novel v-if="activeIndex == '3'"></novel>
        <data-statistics v-if="activeIndex == '4'"></data-statistics>
    </div>
</template>

<script>
	import store from "../store"
    import ghManagement from "./ghManagement.vue";
    import baokuan from "./baokuan.vue";
    import novel from "./novel.vue";
    import dataStatistics from "./dataStatistics.vue";
    export default {
      components: {
        ghManagement,
        baokuan,
        novel,
        dataStatistics
      },
      data() {
        return {
          activeIndex: "1"
        };
      },
      methods: {
        handleSelect(key, keyPath) {
          this.activeIndex = key;
          this.reset()
        },
        reset() {
            store.commit('changeShowTable', true)
            store.commit('changeMenuTable', true)
            store.commit('changeMsgTable', true)
            store.commit('changeShowSetting', false)
            store.commit('changeSettingTable', true)
            store.commit('changeGonghaoUpdate', false)
            store.commit('changeMenuUpdate', false)
            store.commit('changeMsgUpdate', false)
            store.commit('changeSettingUpdate', false)
            store.commit('changeAddSwitch', false)
            store.commit('changeEditSwitch', false)
            store.commit('changeUpdateLinksSwitch', false)
            store.commit('changeAddLinks', false)
            store.commit('changeGoodsShow', false)
            store.commit('changeUpdateGoodsSwitch', false)
        }
      }
    };
</script>

<style scoped>
    .nav {
      width: 100%;
      display: flex;
      flex-direction: row;
      background: #545c64;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 1;
    }
    .logo {
      color: #fff;
      line-height: 60px;
      padding: 0 30px;
    }
    .home .el-menu {
      padding-left: 50px;
      border-bottom-color: #545c64;
    }
</style>




