<template>
    <div class="gh-management">
        <slider-bar :sliderBar="sliderBarList" @select="select"></slider-bar>
        <fans v-if="showIndex == 0"></fans>
    </div>
</template>

<script>
    import sliderBar from "../components/common/sliderBar.vue";
    import fans from "../components/dataStatistics/fans.vue";
    export default {
      name: "ghManagement",
      components: {
        sliderBar,
        fans
      },
      data() {
        return {
          showIndex: 0,
          sliderBarList: [
            { classname: "el-icon-menu", title: "粉丝统计" }
          ]
        };
      },
      methods: {
        select(key) {
          this.showIndex = key;
        }
      }
    };
</script>

<style scoped>
    .gh-management {
      margin-top: 61px;
    }
</style>



