<template>
    <div class="menu-setting">
        <div class="menu-table" v-if="showMenu">
            <el-button class="create" type="primary" @click="create">配置新菜单栏</el-button>
            <br />
            <el-button v-for="(item, index) in menuList" :key="index" type="warning" size="mini" @click="tap(item)">{{item.code}}</el-button>
        </div>
        <edit-info v-else @showAgain="showAgain" :data_info="isCreate ? {} : data_info">{{isCreate ? '配置新菜单栏' : '修改菜单栏配置'}}</edit-info>
        <el-dialog title="提示" :visible.sync="dialogVisible" width="30%">
            <span>您打算进行那种操作？</span>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="updateOne">修改</el-button>
                <el-button type="danger" @click="deleteOne">删除</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import editInfo from "./menu/editInfo.vue";
    import store from "../../store.js";
    import Axios from "axios";
    export default {
      components: {
        editInfo
      },
      computed: {
        showMenu() {
          return store.state.showMenu;
        },
        menuUpdate() {
          return store.state.menuUpdate;
        }
      },
      data() {
        return {
          isCreate: true,
          menuList: [],
          data_info: {},
          dialogVisible: false,
          menuIndex: ""
        };
      },
      mounted() {
        this.showMenuList();
      },
      methods: {
        create() {
          store.commit("changeMenuTable", false);
        },
        tap(item) {
          this.isCreate = false;
          this.dialogVisible = true;
          store.commit("changeMenuUpdate", true);
          this.data_info = item;
        },
        showMenuList() {
          Axios({
            url: "/menu/",
            method: "get"
          }).then(res => {
            this.menuList = res.data.data;
          });
        },
        updateOne() {
          store.commit("changeMenuUpdate", true);
          this.dialogVisible = false;
          store.commit("changeMenuTable", false);
        },
        deleteOne() {
          Axios({
            url: "/menu/del",
            method: "get",
            params: {
              id: this.data_info._id
            }
          }).then(res => {
            let that = this;
            that.dialogVisible = false;
            store.commit("changeMenuUpdate", false);
            this.$confirm("确认删除？")
              .then(_ => {
                that.showMenuList();
                that.$message({
                  type: "success",
                  message: res.data.success
                });
              })
              .catch(_ => {});
          });
        },
        showAgain() {
          this.showMenuList();
        }
      }
    };
</script>

<style scoped>
    .menu-setting {
      padding: 30px;
      margin-left: 200px;
    }
    .create {
      margin-bottom: 30px;
    }
</style>



